<!-- qq群查询界面 -->
<template>
  <div>
    <el-container>
<!--  搜索框   -->
      <el-header>
          <el-input
            placeholder="请输入具体查询内容"
            v-model="searchValue"
            class="input-with-select"
          >
            <template #prepend>
              <el-select v-model="select" placeholder="请选择查询类型" style="width: 160px">
                <el-option label="QQ群号" value="1"></el-option>
                <el-option label="创建日期" value="2"></el-option>
                <el-option label="群名称" value="3"></el-option>
                <el-option label="群简介" value="4"></el-option>
              </el-select>
            </template>
            <template #append>
              <el-button icon="el-icon-search" @click="search(searchValue,select)"></el-button>
            </template>
          </el-input>
      </el-header>
<!-- 搜索结果表格 -->
      <el-main>
        <template>
          <el-table
            v-loading="loading"
            :data="tableData"
            height="520"
            stripe
            style="width: 100%">
            <el-table-column
              prop="QunNum"
              label="群号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="Title"
              label="群名称"
              width="180">
            </el-table-column>
            <el-table-column
              prop="MastQQ"
              label="群人数"
              width="180">
            </el-table-column>
            <el-table-column
              prop="CreateDate"
              label="创建日期"
              width="180">
            </el-table-column>
            <el-table-column
              prop="QunText"
              label="群简介"
              >
            </el-table-column>
          </el-table>
        </template>

      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios';
let basePost = axios.create({
  method: 'post',
  headers:{
    "Access-Control-Allow-Origin":"*",
  },
  baseURL:'http://49.235.79.187:8000',
})

export default {
  name: "QQ_groups",
  data() {
    return {
      loading:false,
      searchValue: '',
      select: '',
      tableData:[]
    };
  },
  methods:{
    //默认情况下显示的数据
    defaultSearch(){
      // 使用 axios发送请求 按群号进行搜索
      basePost.post('/sgk/defaultQuery').then(successResponse => {
        // 传入一个json数组
        let jsonGroups = successResponse.data
        // 判断是否查询到数据是否为空
        if(JSON.stringify(jsonGroups) === '[]'){
          this.$message({
            type: 'warning',
            message: `查询结果为空`
          });
        }
        else {
          //表中添加数据
          for(let i = 0;i<jsonGroups.length;i++){
            this.tableData.push(jsonGroups[i])
          }
          this.$message({
            type: 'success',
            message: `默认查询成功`
          });
        }
      })
    },
    search(searchValue,select){
      if(select === ''){
        this.$message({
          type: 'warning',
          message: '请先选择查询类型'
        })
        return
      }
      if(searchValue === ''){
        this.$message({
          type: 'warning',
          message: '请先输入要查询的值'
        })
        return
      }
      this.tableData = []
      this.loading = true
      if(select === '1'){
        // 使用 axios发送请求 按群号进行搜索
        basePost.post('/sgk/queryByNum','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            let jsonGroup = successResponse.data
            // 检查查询结果是否为空
            this.tableData.push(jsonGroup)
            this.$message({
              type: 'success',
              message: `查询成功,共一条数据`
            });
          }
        }).catch(failResponse => {
          this.$message({
            type: 'warning',
            message: '查询结果为空'
          })
        })
      }else if (select === '2'){
        // 使用 axios发送请求 按创建日期进行搜索
        basePost.post('/sgk/queryByCreateDate','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据是否为空
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }else if (select === '3'){
        // 使用 axios发送请求 按群名称进行搜索
        basePost.post('/sgk/queryByTitle','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }else if (select === '4'){
        // 使用 axios发送请求 按群简介进行搜索
        basePost.post('/sgk/queryByQunText','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }
      this.loading = false
    }
  },
  mounted() {
    this.defaultSearch()
  }

}
</script>

<style scoped>
.el-input {
  border-radius: 30px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>



