import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '../view/HelloWorld'
import QQ_groups from "../components/QQ_groups";
import QQ_num from "../components/QQ_num";

Vue.use(Router)

export default new Router({
  routes: [
    {
      //初始界面
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children:[
        {
          path:'',
          component:QQ_groups
        },
        {
          path: '/groups',
          component: QQ_groups
        },
        {
          path: '/telephone',
          component: QQ_num
        },
      ]
    },
  ]
})



