<template>
  <div class="hello">
    <el-container>
      <el-header>
          <el-menu
            :default-active="activeIndex2"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="#409EFF"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item index="1">QQ_groups</el-menu-item>
            <el-menu-item index="2">QQ号查询</el-menu-item>
            <el-menu-item index="3">数据分析</el-menu-item>
            <el-menu-item index="4"><a href="" target="_blank">更多连接</a></el-menu-item>
          </el-menu>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  components:{
  },
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1'
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      if(key === '1'){
        this.$router.push({
          path:'/groups'
        })
      }else if (key === '2'){
        this.$router.push({
          path:'/telephone'
        })
      }
    },



  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  display: flex;
  text-align: center;
  align-items: center;
  margin-top: 0;
  padding: 0;
}

.el-header, .el-footer {
  text-align: center;
  height: 200px;
  padding: 0;
}
.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
  height: 660px;
}

</style>



