<template>
  <div>
    <div>
      <el-container>
        <!--  搜索框   -->
        <el-header>
          <el-input
            placeholder="请输入具体查询内容"
            v-model="searchValue"
            class="input-with-select"
          >
            <template #prepend>
              <el-select v-model="select" placeholder="请选择查询类型" style="width: 160px">
                <el-option label="QQ号" value="1"></el-option>
                <el-option label="昵称" value="2"></el-option>
                <el-option label="年龄" value="3"></el-option>
                <el-option label="QQ群号" value="4"></el-option>
              </el-select>
            </template>
            <template #append>
              <el-button icon="el-icon-search" @click="search(searchValue,select)"></el-button>
            </template>
          </el-input>
        </el-header>
        <!-- 搜索结果表格 -->
        <el-main>
          <template>
            <el-table
              v-loading="loading"
              :data="tableData"
              height="520"
              stripe
              style="width: 100%">
              <el-table-column
                prop="QQNum"
                label="QQ号"
                width="180">
              </el-table-column>
              <el-table-column
                prop="Nick"
                label="昵称"
                width="180">
              </el-table-column>
              <el-table-column
                prop="Age"
                label="年龄"
                width="180">
              </el-table-column>
              <el-table-column
                prop="Gender"
                label="性别"
                width="180">
              </el-table-column>
              <el-table-column
                prop="Auth"
                label="Auth"
                width="180">
              </el-table-column>
              <el-table-column
                  prop="QunNum"
                  label="加入的QQ群号">
              </el-table-column>
            </el-table>
          </template>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
let basePost = axios.create({
  method: 'post',
  headers:{
    "Access-Control-Allow-Origin":"*",
  },
  baseURL:'http://49.235.79.187:8000',
})

export default {
  name: "QQ_num",
  data() {
    return {
      loading:false,
      searchValue: '',
      select: '',
      tableData:[]
    };
  },
  methods:{
    //默认情况下显示的数据
    defaultSearch(){
      // 使用 axios发送请求 按群号进行搜索
      basePost.post('/sgk/defaultQuery2').then(successResponse => {
        // 传入一个json数组
        let jsonGroups = successResponse.data
        // 判断是否查询到数据是否为空
        if(JSON.stringify(jsonGroups) === '[]'){
          this.$message({
            type: 'warning',
            message: `查询结果为空`
          });
        }
        else {
          //表中添加数据
          for(let i = 0;i<jsonGroups.length;i++){
            this.tableData.push(jsonGroups[i])
          }
          this.$message({
            type: 'success',
            message: `默认查询成功`
          });
        }
      })
    },
    search(searchValue,select){
      if(select === ''){
        this.$message({
          type: 'warning',
          message: '请先选择查询类型'
        })
        return
      }
      if(searchValue === ''){
        this.$message({
          type: 'warning',
          message: '请先输入要查询的值'
        })
        return
      }
      this.tableData = []
      this.loading = true
      if(select === '1'){
        // 使用 axios发送请求 按群号进行搜索
        basePost.post('/sgk/queryByQQNum','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            let jsonGroup = successResponse.data
            // 检查查询结果是否为空
            this.tableData.push(jsonGroup)
            this.$message({
              type: 'success',
              message: `查询成功,共一条数据`
            });
          }
        }).catch(failResponse => {
          this.$message({
            type: 'warning',
            message: '查询结果为空'
          })
        })
      }else if (select === '2'){
        // 使用 axios发送请求 按创建日期进行搜索
        basePost.post('/sgk/queryByNick','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据是否为空
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }else if (select === '3'){
        // 使用 axios发送请求 按群名称进行搜索
        basePost.post('/sgk/queryByAge','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }else if (select === '4'){
        // 使用 axios发送请求 按群简介进行搜索
        basePost.post('/sgk/queryByQunNum','index='+searchValue).then(successResponse => {
          if(successResponse.status === 200){
            // 传入一个json数组
            let jsonGroups = successResponse.data
            // 判断是否查询到数据
            if(JSON.stringify(jsonGroups) === '[]'){
              this.$message({
                type: 'warning',
                message: `查询结果为空`
              });
            }
            else {
              //表中添加数据
              for(let i = 0;i<jsonGroups.length;i++){
                this.tableData.push(jsonGroups[i])
              }
              this.$message({
                type: 'success',
                message: '查询成功,共'+jsonGroups.length+'条数据'
              });
            }
          }
        }).catch(failResponse => {
          this.$message({
            type: 'error',
            message: '查询失败'
          })
        })
      }
      this.loading = false
    }
  },
  mounted() {
    this.defaultSearch()
  }
}
</script>

<style scoped>

</style>



